import { getContext, getElement, store } from '@wordpress/interactivity';
const apiFetch = window.wp.apiFetch;

const { state } = store( 'nextEventBlock', {
	state: {
		event: ''
	},
	actions: {
		getNextEvent: () => {
			const { ref } = getElement();
			const context = getContext();

			const queryArgs = [
				`tags=${context.tags}`
			];

			apiFetch({
				// TODO: It sure would be nice to use addQueryArgs to build the path, but WordPress doesn't support adding scripts to script modules yet; that's also why apiFetch is being called in a janky way
				path: `/simplecal/v1/events/next-event/?${queryArgs.join('&')}`
			}).then(
				(response) => {
					state.event = response.event;
				}
			);
			
		}
	},
	callbacks: {
		showNextEvent: () => {
			const { ref } = getElement();
			ref.innerHTML = state.event;
		}
	}
});